import React from "react";
import Button from './Button';
import styles from "./components.module.scss";

function Header (){
  return(
    <div className={styles.header}>
      <div className={styles.btn_container}>
        <Button title='< ' href='/'/>
      </div>
      <div className={styles.btn_container}>
      <Button href="auth" title="" />
      </div>
  </div>  )
}
export default Header